import service from '@/utils/request';
export const GetTodayDailyStudyAsync = () => service.request({
    url: '/api/daily-study/today/',
    method: 'get'
});
export const SendDailyStudyAsync = (id, studies) => service.request({
    url: '/api/daily-study/' + id + '/',
    method: 'put',
    data: {
        studies
    }
});
export const AdminGetDailyStudyByIdAsync = (dailyStudyId, urlParams = {}) => service.request({
    url: `/api/admin/daily-study/${dailyStudyId}/`,
    method: 'get',
    params: urlParams
});
export const AdminGetTodayDailyStudy = (userId) => service.request({
    url: `/api/admin/daily-study/today/user/${userId}/`,
    method: 'get'
});
export const AdminSendDailyStudy = (dailyStudyId, studies, userId) => service.request({
    url: `/api/admin/daily-study/${dailyStudyId}/`,
    method: 'put',
    data: {
        studies: studies,
        user: userId
    }
});
export const AdminGetDailyStudies = (urlParams = {}) => service.request({
    url: '/api/admin/daily-study/',
    method: 'get',
    params: urlParams
});
export const AdminValidateDailyStudy = (dailyStudyId) => service.request({
    url: `/api/admin/daily-study/${dailyStudyId}/validate/`,
    method: 'put'
});
export const AdminInvalidateDailyStudy = (dailyStudyId) => service.request({
    url: `/api/admin/daily-study/${dailyStudyId}/invalidate/`,
    method: 'put'
});
